module.exports = (state, x, y, exeption) => {
  const xNum = Math.floor(x / state.pixelSize);
  const yNum = Math.floor(y / state.pixelSize);
  const index = yNum * state.cols + xNum;
  if (index !== exeption && xNum >= 0 && yNum >= 0
      && xNum < state.cols && yNum < state.rows) {
    return { indx: index, x: xNum, y: yNum };
  }
  return { indx: undefined, x: undefined, y: undefined };
};
