/* eslint-disable no-param-reassign */

// cursors
import PENCIL_CURSOR from './img/pencil.png';
import BUCKET_CURSOR from './img/bucket.png';
import PICKER_CURSOR from './img/picker.png';
import ERASER_CURSOR from './img/eraser.png';

export default (state) => {
  state.bucketClick = () => {
    state.tool = 'bucket';
    state.cleanTools();
    state.bucket.style.background = state.COLORS.selectedTool;
    state.canvas.style.cursor = `url(${BUCKET_CURSOR}) 4 8, auto`;
  };

  state.pencilClick = () => {
    state.tool = 'pencil';
    state.cleanTools();
    state.color = state.colorSelector.value;
    state.pencil.style.background = state.COLORS.selectedTool;
    state.canvas.style.cursor = `url(${PENCIL_CURSOR}) 0 30, auto`;
  };

  state.pickerClick = () => {
    state.tool = 'picker';
    state.cleanTools();
    state.picker.style.background = state.COLORS.selectedTool;
    state.canvas.style.cursor = `url(${PICKER_CURSOR}) 0 30, auto`;
  };

  state.resetClick = () => {
    if (state.animating) { state.play(); }
    state.frames[state.currentFrame].reset();
  };

  state.eraserClick = () => {
    state.tool = 'eraser';
    state.cleanTools();
    state.eraser.style.background = state.COLORS.selectedTool;
    state.canvas.style.cursor = `url(${ERASER_CURSOR}) 0 30, auto`;
    state.color = state.COLORS.transparent;
  };

  state.canvasClick = (event) => {
    if (state.animating) { state.play(); }
    state.frames[state.currentFrame].click(event);
  };

  state.canvasMouseDown = (event) => {
    if (state.animating) {
      state.play();
      event.stopPropagation();
    }

    state.frames[state.currentFrame].mouseDown(event);
  };

  state.keyHandler = (event) => {
    switch (event.code) {
      case 'KeyB':
        state.bucketClick();
        break;
      case 'KeyP':
        state.pencilClick();
        break;
      case 'KeyE':
        state.eraserClick();
        break;
      case 'KeyR':
        state.resetClick();
        break;
      case 'KeyA':
        state.play();
        break;
      case 'KeyF':
        state.full();
        break;
      case 'KeyS':
        state.frameManager.saveToFile();
        break;
      case 'KeyL':
        state.fileInput.click();
        break;
      case 'KeyO':
        state.frameManager.loadFromLocalStorage.call(state.frameManager);
        break;
      case 'KeyT':
        state.frameManager.saveToLocalStorage.call(state.frameManager);
        break;
      default:
    }
  };


  state.bodyMouseUp = (event) => {
    if (event.target.id !== 'palette') {
      state.canvas.onmouseup = null;
      state.canvas.onmousemove = null;
    }
  };

  state.selectColor = () => {
    state.changeColor(state.colorSelector.value);
  };

  state.fpsClick = (event) => {
    if (event) {
      if (event.target.id === '4fps') state.setFps(4);
      if (event.target.id === '8fps') state.setFps(8);
      if (event.target.id === '16fps') state.setFps(16);
    }
    if (state.animating) {
      state.play();
      state.play();
    }
    state.playPreview();
  };

  state.fpsChange = () => {
    state.setFps(document.getElementById('fps-range').value);
  };

  state.sizeManagerClick = (event) => {
    if (event.target.id.slice(0, 4) === 'size') {
      const newSize = +event.target.id.slice(4);
      if (newSize !== state.currentResolution.cols) {
        state.frameManager.resizePalette(newSize);
        document.getElementById('size32').classList.remove('size-button-selected');
        document.getElementById('size64').classList.remove('size-button-selected');
        document.getElementById('size128').classList.remove('size-button-selected');
        event.target.classList.add('size-button-selected');
      }
    }
  };

  state.fileInputHandler = (event) => {
    const reader = new FileReader();
    reader.onloadend = (evt) => {
      if (evt.target.readyState === FileReader.DONE) {
        const fileString = evt.target.result;
        const loadedData = JSON.parse(fileString);
        state.frameManager.loadProject(loadedData);
      }
    };
    reader.readAsBinaryString(event.target.files[0]);
  };
};
