import TEMPLATE from './template.html';

import Frame from '../Frame';

import './style.scss';

export default class FrameManager {
  constructor(state) {
    this.state = state;
    const element = document.createElement('div');
    element.innerHTML = TEMPLATE;
    const canvasWrapper = document.getElementById('canvas-wrapper');
    canvasWrapper.parentNode.insertBefore(element, canvasWrapper);

    this.state.frames = [];
    this.state.previews = [];
    this.createNewFrame();

    document.getElementById('new-frame').addEventListener('click', this.createNewFrame.bind(this));
  }

  loadProject(loadedData) {
    this.cleanProject();
    this.state.frames = [];
    loadedData.frames.forEach(() => {
      this.createNewFrame();
    });
    this.state.setFps(loadedData.fps);
    this.state.previews = [];

    this.renumerate();

    document.getElementById(`size${loadedData.currentResolution.cols}`).click();

    this.state.frames.forEach((frame) => {
      frame.load(loadedData);
    });

    this.state.frames.forEach((frame) => {
      frame.draw();
    });
  }

  getDataToSave() {
    const dataToSave = {};
    dataToSave.frames = [];
    this.state.frames.forEach((frame) => {
      dataToSave.frames.push(frame.pixels);
    });
    dataToSave.currentResolution = this.state.currentResolution;
    dataToSave.fps = this.state.fps;
    return JSON.stringify(dataToSave);
  }

  saveToFile() {
    const dataToSave = this.getDataToSave();

    const link = document.createElement('a');
    link.download = 'animation.piskelc';
    const blob = new Blob([dataToSave], { type: 'text/plain' });
    link.href = window.URL.createObjectURL(blob);
    link.click();
  }

  saveToLocalStorage() {
    const dataToSave = this.getDataToSave();
    localStorage.animation = dataToSave;
  }

  loadFromLocalStorage() {
    if (localStorage.animation) {
      const loadedData = JSON.parse(localStorage.animation);
      this.loadProject(loadedData);
    }
  }

  cleanProject() {
    this.a = 0;
    const frameControl = document.getElementById('frame-control');
    this.state.currentFrame = 0;
    this.state.previewFrame = 0;
    // for some reason first child is undefined,
    // so I have to use next sibling to make it work right
    let curFrame = frameControl.firstChild.nextSibling;
    while (curFrame.id !== 'new-frame') {
      const delFrame = curFrame;
      curFrame = curFrame.nextSibling;
      delFrame.remove();
    }
  }

  resizePalette(newSize) {
    this.state.currentResolution = { cols: newSize, rows: newSize };
    this.state.frames.forEach((frame) => {
      frame.resize(newSize);
    });
  }

  showCoordinats(event) {
    this.state.frames[this.state.currentFrame].defaultMove(event);
  }

  swapFrames(frame1, frame2) {
    // swap frames
    let buf = this.state.frames[frame1];
    this.state.frames[frame1] = this.state.frames[frame2];
    this.state.frames[frame2] = buf;
    // and previews
    buf = this.state.previews[frame1];
    this.state.previews[frame1] = this.state.previews[frame2];
    this.state.previews[frame2] = buf;
  }

  static moveDownFrame(downEvent) {
    const movingFrame = downEvent.target.parentNode;
    let movingFrameNumber = +movingFrame.id.slice(5);
    movingFrame.style['z-index'] = 1;
    document.body.style.cursor = 'move';
    const initialPos = {
      x: downEvent.clientX,
      y: downEvent.clientY,
    };

    function moveMoveFrame(moveEvent) {
      const translation = {
        x: moveEvent.clientX - initialPos.x,
        y: moveEvent.clientY - initialPos.y,
      };
      // move down
      if (translation.y > 111 && movingFrame.nextSibling.id !== 'new-frame') {
        movingFrame.parentNode.insertBefore(movingFrame, movingFrame.nextSibling.nextSibling);
        initialPos.y = moveEvent.clientY;
        translation.y = 0;

        this.swapFrames(movingFrameNumber, movingFrameNumber + 1);

        movingFrameNumber += 1;
      }
      // move up
      if (translation.y < -111 && movingFrame.previousSibling.previousSibling) {
        movingFrame.parentNode.insertBefore(movingFrame, movingFrame.previousSibling);
        initialPos.y = moveEvent.clientY;
        translation.y = 0;

        this.swapFrames(movingFrameNumber, movingFrameNumber - 1);

        movingFrameNumber -= 1;
      }
      movingFrame.style.transform = `translateY(${translation.y}px)`;
    }
    function moveUpFrame() {
      document.body.style.cursor = 'inherit';
      movingFrame.style.transform = 'translateY(0px)';
      movingFrame.style.transform = 'scale(1)';

      document.onmousemove = null;
      document.onmouseup = null;
      this.state.currentFrame = movingFrameNumber;
      this.renumerate();
      this.state.frames[this.state.currentFrame].draw();
    }
    document.onmousemove = moveMoveFrame.bind(this);
    document.onmouseup = moveUpFrame.bind(this);
  }

  renumerate() {
    const frameControl = document.getElementById('frame-control');
    let counter = 0;
    // for some reason first child is undefined,
    // so I have to use next sibling to make it work right
    let curFrame = frameControl.firstChild.nextSibling;
    while (curFrame.id !== 'new-frame') {
      curFrame.id = `frame${counter}`;
      curFrame.innerHTML = `
      <div class="frame-number" id="number${counter}">${counter + 1}</div>
      <div class="delete-frame" id="delete${counter}"></div>

      <img class="frame-preview" id="frame-preview${counter}"
          src="${this.state.previews[counter]}" alt="preview"></img>

      <div class="move-frame" id="move${counter}"></div>
      <div class="copy-frame" id="copy${counter}"></div>
      `;
      curFrame.style.border = '2px solid gray';
      curFrame.style['z-index'] = 0;

      document.getElementById(`move${counter}`).addEventListener('mousedown', FrameManager.moveDownFrame.bind(this));

      curFrame.onclick = this.switchFrame.bind(this);

      curFrame = curFrame.nextSibling;

      this.state.frames[counter].frameNumber = counter;

      counter += 1;
    }
    if (document.getElementById(`frame${this.state.currentFrame}`)) {
      document.getElementById(`frame${this.state.currentFrame}`).style.border = '2px solid yellow';
    }

    // don't show unnesessary buttons if only one frame left
    if (this.state.frames.length === 1) {
      document.getElementById(`delete${this.state.currentFrame}`).remove();
      document.getElementById(`move${this.state.currentFrame}`).remove();
    }
  }

  switchFrame(event) {
    // switch
    if (event.target.id.slice(0, 13) === 'frame-preview') {
      if (this.state.animating) { this.state.play(); }
      const frame = +event.target.id.slice(13);
      document.getElementById(`frame${frame}`).style.border = '2px solid yellow';
      this.state.currentFrame = frame;
    }
    if (event.target.id.slice(0, 6) === 'number') {
      if (this.state.animating) { this.state.play(); }
      const frame = +event.target.id.slice(6);
      document.getElementById(`frame${frame}`).style.border = '2px solid yellow';
      this.state.currentFrame = frame;
    }
    // delete
    if (event.target.id.slice(0, 6) === 'delete' && this.state.frames.length > 1) {
      const delIndex = +event.target.id.slice(6);
      this.state.frames.splice(delIndex, 1);
      this.state.previews.splice(delIndex, 1);
      if (this.state.currentFrame >= this.state.frames.length) {
        this.state.currentFrame = this.state.frames.length - 1;
      }
      this.state.previewFrame = 0;
      event.target.parentNode.remove();
    }
    // duplicate
    if (event.target.id.slice(0, 4) === 'copy') {
      const index = +event.target.id.slice(4);
      this.state.frames.splice(index, 0, Frame.clone(this.state.frames[index]));
      this.state.previews.splice(index, 0, this.state.previews[index]);
      this.state.currentFrame = index + 1;
    }
    this.renumerate();
    this.state.frames[this.state.currentFrame].draw();
  }

  createNewFrame() {
    this.state.currentFrame = this.state.frames.length;
    this.state.frames.push(new Frame(this.state));
    this.state.frames[this.state.currentFrame].draw();
    this.renumerate();
  }
}
