import TEMPLATE_MAIN from './template.html';

import PENCIL_CURSOR from './img/pencil.png';

import getEventHandlers from './eventHandlers';
import getHelpers from './helpers';
import FrameManager from './components/FrameManager';

import CONSTANTS from './CONSTANTS';


export default class App {
  constructor(state) {
    this.state = state;
  }

  init() {
    const { COLORS, RESOLUTIONS } = CONSTANTS;

    // main object, that stores common data
    const state = {
      COLORS,
      RESOLUTIONS,
      currentResolution: RESOLUTIONS.medium,
      tool: 'pencil',
      color: COLORS.black,
      prevColor: COLORS.gray,
      currentFrame: 0,
      previewFrame: 0,
      animating: false,
      fps: 8,
    };

    this.state = state;

    getHelpers(state);
    getEventHandlers(state);

    document.body.innerHTML = TEMPLATE_MAIN;
    // canvas
    state.canvas = document.getElementById('palette');
    state.ctx = state.canvas.getContext('2d');
    state.canvas.onclick = state.canvasClick;
    state.canvas.onmousedown = state.canvasDown;
    state.canvas.onmousedown = state.canvasMouseDown;
    // tools
    state.reset = document.getElementById('reset');
    state.reset.onclick = state.resetClick;
    state.bucket = document.getElementById('bucket');
    state.bucket.onclick = state.bucketClick;
    state.picker = document.getElementById('picker');
    state.picker.onclick = state.pickerClick;
    state.pencil = document.getElementById('pencil');
    state.pencil.addEventListener('click', state.pencilClick);
    state.eraser = document.getElementById('eraser');
    state.eraser.addEventListener('click', state.eraserClick);
    state.canvas.style.cursor = `url(${PENCIL_CURSOR}) 0 30, auto`;
    // add event listners
    document.addEventListener('keydown', state.keyHandler);
    // colors
    state.current = document.getElementById('current');
    state.colorSelector = document.getElementById('color-selector');
    state.current.onclick = state.selectColor;
    state.prev = document.getElementById('prev');
    state.prev.onclick = function changeToPrev() { state.changeColor(state.prevColor); };
    state.prevInd = document.getElementById('prev-indicator');
    state.red = document.getElementById('red');
    state.red.onclick = function changeToRed() { state.changeColor(state.COLORS.red); };
    state.blue = document.getElementById('blue');
    state.blue.onclick = function changeToBlue() { state.changeColor(state.COLORS.blue); };
    //
    state.pencil.style.background = 'rgb(219, 219, 219)';
    state.changeColor(state.color);
    //
    state.sizeManager = document.getElementById('size-manager');
    state.sizeManager.addEventListener('click', state.sizeManagerClick);

    //
    document.getElementById('color-selector').addEventListener('change', state.selectColor);
    //
    state.fileInput = document.getElementById('open-file');
    state.fileInput.addEventListener('change', state.fileInputHandler, false);
    document.getElementById('play').addEventListener('click', state.play);
    document.getElementById('fullscreen').addEventListener('click', state.full);
    document.getElementById('load').addEventListener('click', () => {
      state.fileInput.click();
    });
    document.getElementById('save').addEventListener('click', () => {
      state.frameManager.saveToFile();
    });
    document.getElementById('fps-wrapper').addEventListener('click', state.fpsClick);
    document.getElementById('fps-range').addEventListener('input', state.fpsChange);

    document.addEventListener('mouseup', state.bodyMouseUp);

    state.frameManager = new FrameManager(state);

    document.getElementById('local-load').addEventListener('click',
      state.frameManager.loadFromLocalStorage.bind(state.frameManager));
    document.getElementById('local-save').addEventListener('click',
      state.frameManager.saveToLocalStorage.bind(state.frameManager));

    state.coordinats = document.getElementById('coordinats');
    const notthis = {};
    notthis.state = state;
    state.canvas.addEventListener('mousemove', state.frameManager.showCoordinats.bind(notthis));

    state.playPreview();
  }
}
